<script>
	export default {
		onLaunch() {

			//隐藏底部导航栏
			uni.hideTabBar()
			console.log('App Launch')
			wx.getSystemInfo({
				success: res => {
					this.globalData.statusBarHeight = res.statusBarHeight
					// 判断是否全面屏
					if (res.statusBarHeight > 40) {
						this.globalData.fullScreen = true
					} else {
						this.globalData.fullScreen = false
					}
				}
			})
			const value = uni.getStorageSync('isuser');
			if (value == "0") {
				this.globalData.navbar_list = this.globalData.navbar_admin
				this.globalData.sort = 0
				uni.switchTab({
					url: '/pages/appraisalPool/appraisalPool'
				})
			} else {
				this.globalData.navbar_list = this.globalData.navbar_user
				this.globalData.sort = 1
				uni.switchTab({
					url: '/pages/index/index'
				})
			}
		},
		onShow() {
			const value = uni.getStorageSync('isuser');
			if (value == "0") {
				this.globalData.navbar_list = this.globalData.navbar_admin
				this.globalData.sort = 0
			} else {
				this.globalData.navbar_list = this.globalData.navbar_user
				this.globalData.sort = 1
			}
			console.log('App Show', value)
		},
		onChageState() {
			const value = uni.getStorageSync('isuser');
			if (value == "0") {
				this.globalData.navbar_list = this.globalData.navbar_admin
				this.globalData.sort = 0
			} else {
				this.globalData.navbar_list = this.globalData.navbar_user
				this.globalData.sort = 1
			}
			this.globalData.now_page_index = 0;
		},
		onHide() {
			console.log('App Hide')
		},
		globalData: {
			fullScreen: false, //是否全面屏
			now_page_index: 0, //底部导航状态
			statusBarHeight: '', //顶部导航栏高度
			//img_src: "http://wxhf.var365.cn", //服务器图片地址
			img_src:"https://www.bopo.com",
			sort: 1, //用户权限  1用户 0鉴定师
			navbar_user: [{
					path: '/pages/index/index',
					name: '首页',
					iconPath: 'data:image/png;base64,',
					selectedIconPath: 'data:image/png;base64,'
				},
				{
					path: '/pages/mine/mine',
					name: '我的',
					iconPath: 'data:image/png;base64,',
					selectedIconPath: 'data:image/png;base64,'
				}
			],
			navbar_admin: [{
					path: '/pages/appraisalPool/appraisalPool',
					name: '鉴定池',
					iconPath: 'data:image/png;base64,',
					selectedIconPath: 'data:image/png;base64,'
				},
				{
					path: '/pages/orderList/orderList',
					name: '订单',
					iconPath: 'data:image/png;base64,',
					selectedIconPath: 'data:image/png;base64,'
				},
				{
					path: '/pages/appraisalMine/appraisalMine',
					name: '我的',
					iconPath: 'data:image/png;base64,',
					selectedIconPath: 'data:image/png;base64,'
				}
			],
			navbar_list: [] //底部导航
		}
	}
</script>

<style lang="less">
	/*每个页面公共css */
	html,
	body,
	div,
	span,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p,
	a,
	q,
	em,
	ul,
	li,
	ol,
	dl,
	dt,
	dd,
	form,
	article,
	aside,
	embed,
	table,
	td,
	th,
	input,
	button,
	legend,
	fieldlset,
	blockquote,
	textarea,
	label,
	strong,
	img,
	cite {
		margin: 0;
		padding: 0;
		outline: none;
	}

	body {
		font-family: '微软雅黑';
		font-size: 24upx;
		line-height: 1;
		margin: 0 auto;
		color: #333333;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-weight: normal;
		font-size: 100%;
	}

	table {
		border-collapse: collapse;
	}

	ul,
	li,
	ol,
	li {
		list-style: none;
	}

	img {
		border: none;
		vertical-align: top;
	}

	em,
	cite,
	address,
	i {
		font-style: normal;
		font-weight: normal;
	}

	a {
		text-decoration: none;
		color: #333333;
	}

	input {
		outline: none;
		border: none;
		background-color: transparent;
	}

	input::-ms-clear {
		display: none;
	}

	textarea {
		outline: none;
		resize: none;
		;
		overflow: auto;
		border: none;
		background: transparent;
	}

	.page {
		min-height: 100vh;
		box-sizing: border-box;
	}

	page,
	view {
		font-size: 24upx;
		color: #202022;
	}

	image {
		width: auto;
		height: auto;
	}

	.x_countW {
		width: 690upx;
		margin: 0 auto;
	}

	// 自定义导航栏
	.page {
		width: 100%;
		background: url(static/navtop_bg.png) no-repeat center -50upx;
		background-size: 100% 530upx;

		&.fullScreen {
			background: url(static/navtop_bg.png) no-repeat center top;
			background-size: 100% 530upx;
		}
	}

	//隐藏滚动条
	::-webkit-scrollbar {
		width: 0;
		height: 0;
		color: transparent;
	}

	// 一行超出隐藏
	.text-hidden1 {
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}

	// 两行行超出隐藏
	.text-hidden2 {
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
	}

	// 三行行超出隐藏
	.text-hidden3 {
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 3;
		overflow: hidden;
	}

	/* 公共标题 */
	.count-tit {
		text-align: center;

		.h3 {
			font-size: 40upx;
			line-height: 56upx;
			color: #444444;
			font-weight: bold;

			&::before,
			&::after {
				content: '';
				display: inline-block;
				vertical-align: middle;
				width: 42upx;
				height: 1upx;
				background: #333333;
				margin: 0 30upx;
			}
		}

		.p {
			font-size: 20upx;
			color: #999999;
			line-height: 28upx;
		}
	}

	.flex {
		display: flex;
		text-align: center;
	}

	// css图标
	.css-icon {
		display: inline-block;
		height: 1em;
		width: 1em;
		font-size: 20upx;
		box-sizing: border-box;
		text-indent: -9999px;
		vertical-align: middle;
		position: relative;
	}

	.css-icon::before,
	.css-icon::after {
		content: '';
		box-sizing: inherit;
		position: absolute;
		left: 50%;
		top: 50%;
		-ms-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
	}

	// < 箭头
	.icon-arrow-left::before {
		height: 1.4em;
		width: 1.4em;
		border-style: solid;
		border-width: 4upx 0 0 4upx;
		-ms-transform: translate(-25%, -50%) rotate(-45deg);
		transform: translate(-25%, -50%) rotate(-45deg);
	}

	// > 箭头
	.icon-arrow-right::before {
		height: .65em;
		width: .65em;
		border-style: solid;
		border-width: 2upx 0 0 2upx;
		-ms-transform: translate(-75%, -50%) rotate(135deg);
		transform: translate(-75%, -50%) rotate(135deg);
	}

	// × 叉号
	.icon-close::before {
		width: 90%;
		border-top: 4upx solid;
		-ms-transform: translate(-50%, -50%) rotate(45deg);
		transform: translate(-50%, -50%) rotate(45deg);
	}

	.icon-close::after {
		height: 90%;
		border-left: 4upx solid;
		-ms-transform: translate(-50%, -50%) rotate(45deg);
		transform: translate(-50%, -50%) rotate(45deg);
	}

	// √ 对勾
	.icon-check::before {
		border-style: solid;
		border-width: 0 0 2px 2px;
		height: .5em;
		width: .9em;
		-ms-transform: translate(-50%, -.375em) rotate(-45deg);
		transform: translate(-50%, -.375em) rotate(-45deg);
	}

	// 底部无数据分割线
	.no-data {
		text-align: center;
		font-size: 26upx;
		color: #666;
		padding: 20upx 0;

		&::after,
		&::before {
			content: '';
			display: inline-block;
			vertical-align: middle;
			width: 120upx;
			height: 1upx;
			background: #666;
			margin: 0 30upx;
		}
	}
</style>
